<template>
  <div class="box">
   <Header>
      <div slot="center">搜索</div>
    </Header>
    <div class="content">
      <ul class="sousuolan">
        <li><span class="iconfont icon-zuo" @click="goback()"></span></li>
        <li><input type="text" placeholder="输入你要搜索的关键词" v-model="text"></li>
        <li @click="search()"><div>搜索</div></li>
      </ul>
      <div class="hot">
        <div class="hot-top">
          <p>热门搜索</p>
          <p>隐藏</p>
        </div>
        <div class="hot-end">
          <p @click="hotbtn('火龙果')">火龙果</p>
          <p @click="hotbtn('香蕉')">香蕉</p>
          <p @click="hotbtn('芒橙')" >芒橙</p>
          <p @click="hotbtn('猕猴桃')">猕猴桃</p>
          <p @click="hotbtn('柚子')">柚子</p>
          <p @click="hotbtn('桃子')">桃子</p>
        </div>
      </div>
      <Prolist :prolist="prolist" />
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Prolist from '@/components/Prolist'
import Vue from 'vue'
import { Toast } from 'vant'
Vue.use(Toast)
export default {
  components: {
    Prolist
  },
  data () {
    return {
      prolist: [],
      text: localStorage.getItem('text')
    }
  },
  created () {
    // let token = localStorage.getItem('token')
    let url = '/pro/search?text=' + localStorage.getItem('text')
    //  + '&token=' + token
    axios.get(url).then(res => {
      console.log(res.data)
      if (res.data.code === '10119') {
        this.$router.push('/login')
      } else {
        this.prolist = res.data.data
      }
    })
  },
  // 路由的导航守卫
  beforeRouteLeave (to, from, next) {
    localStorage.setItem('text', this.text)
    next()
  },
  methods: {
    goback () {
      this.$router.back()
    },
    search () {
      let url = '/pro/search?text=' + this.text
      axios.get(url).then(res => {
        if (res.data.code === '10119') {
          this.$router.push('/login')
        } else {
          if (res.data.data.length < 1) {
            Toast('对不起，暂未收录该类动漫')
          } else {
            this.prolist = res.data.data
          }
        }
      })
    },
    hotbtn (str) {
      this.text = str
      this.search()
    }
  }
}
</script>
<style lang="scss" scoped>
@import '@/lib/reset.scss';
.sousuolan{
    @include rect(100%, auto);
    @include flexbox();
    @include justify-content(space-between);
    padding:13px 5px;
    @include align-items();
    background: #fff;
    border-bottom:1px solid #eee;
    position: fixed;
    z-index: 99999;
    top: 0;
  li {
    // width:.5rem;
    @include flexbox();
    @include align-items();
    @include color(#fff);
    &:nth-child(1) {
      // @include rect(.3rem, 100%);
      span {
        color:#777;
        font-size:20px;
        font-weight: 700;
        width:.3rem;
      }
    }
    &:nth-child(3){
      // @include rect(1rem, 100%);
      div {
        width:.7rem;
        height:.35rem;
        background: #ff5000;
        border-radius: 20px;
        line-height: .35rem;
        color: #fff;
        text-align: center;
        font-size:16px;
      }
    }
    &:nth-child(2) {
      @include flex();
      input {
        display: block;
        outline: none;
        border: 0;
        background: #f1f1f1;
        border-radius: 20px;
        width: 90%;
        border-bottom: 1px solid #fff;
        color: #000;
        font-size:14px;
        text-indent: 20px;
        margin:  0 5%;
        height:.35rem;
      }
    }
  }
}
.hot {
  @include flexbox();
  flex-wrap: wrap;
  background: #fff;
  font-size:16px;
  padding:5px 10px;
  .hot-top {
  @include flexbox();
  width:100%;
  justify-content: space-between;
  // margin-bottom:5px;
  p {
    &:nth-child(2) {
      color:#aaa;
    }
  }
  }
  .hot-end {
  @include flexbox();
  flex-wrap: wrap;
  padding:10px 0;
  width:100%;
  p {
    // width:.8rem;
    padding:8px 15px;
    background: #f6f6f6;
    margin:5px 15px 5px 0;
    // height:.3rem;
    // line-height: .3rem;
    text-align: center;
    border-radius: 20px;
    font-size:14px;
  }
  }
}
::-webkit-input-placeholder {
  color: #666;
}
</style>
